import stats from 'stats-js';
import browser from 'browser-detect';
//test fps
import $ from 'jquery';
require('bootstrap');

import slick from 'slick-carousel';
import ScrollMagic from 'scrollmagic';
import {
  TweenLite
} from 'gsap';

export default class App {
  constructor() {

  }
  init() {

    this.browser = browser();
    // this.initStats();
    this.initPages();
    this.initHamburger();
    this.addListeners();
    this.animate();
    this.initSlider();
  }
  initPages() {
    var path = window.location.pathname;
    var page = path.split("/").pop();
    // console.log(page);
    $('.nav__item').each(function(){
      if($(this).children("a").attr('href') == page){
        $(this).addClass('is-active');
      }
    })
    var hash = window.location.hash.substr(1);
    if(hash!='') {
        // console.log(hash);
    }
    var val = window.location.href;
    if(val.indexOf('?')!=-1)
    {
      var ary1 = val.split('?');
      var ary2 = ary1[1].split('&');
      var ary3 = ary2[0].split('=');
      var id = ary3[1];
      // console.log(id)
    }
  }
  initHamburger() {
    $('.hamburger').click(function() {
      if ($('.hamburger').hasClass('is-active')) {
        $('.hamburger').removeClass('is-active');
        $('.nav__content').removeClass('is-active');
        $('.nav__shop').removeClass('is-active');
      } else {
        $('.hamburger').addClass('is-active')
        $('.nav__content').addClass('is-active');
        $('.nav__shop').addClass('is-active');
      }
    })
  }
  initSlider() {
    $('.news-page .img__slider').slick({
      infinite: true,
      slidesToShow: 1,
      slidesToScroll: 1,
      prevArrow: false,
      nextArrow: false,
      dots: true
    })
    $('.cal-slider').slick({
      infinite: true,
      slidesToShow: 5,
      slidesToScroll: 1,

    })
    $(".kv-slider").slick({
      infinite: true,
      slidesToShow: 1,
      slidesToScroll: 1,
    });
    $(".news__slider").slick({
      infinite: true,
      slidesToShow: 3,
      slidesToScroll: 3,
      dots: false,
      prevArrow: false,
      nextArrow: false,
      responsive: [

        {
          breakpoint: 992,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2
          }
        },
        {
          breakpoint: 567,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1
          }
        }
      ]
    });
    $('.news .slick-prev').click(function() {
      $(".news__slider").slick('slickPrev');
    });
    $('.news .slick-next').click(function() {
      $(".news__slider").slick('slickNext');
    });
    $(".production__slider").slick({
      infinite: true,
      slidesPerRow: 4,
      rows: 2,
      dots: false,
      prevArrow: false,
      nextArrow: false,
      responsive: [{
          breakpoint: 1200,
          settings: {
            slidesPerRow: 3,
            rows: 2,
          }
        },
        {
          breakpoint: 992,
          settings: {
            slidesPerRow: 2,
            rows: 2,
          }
        },
        {
          breakpoint: 567,
          settings: {
            slidesPerRow: 1,
            rows: 1,
          }
        }
      ]
    });
    $('.production .slick-prev').click(function() {
      $(".production__slider").slick('slickPrev');
    });
    $('.production .slick-next').click(function() {
      $(".production__slider").slick('slickNext');
    });
  }
  //init Stats
  initStats() {
    this.stats = new stats();
    this.stats.showPanel(0); // 0: fps, 1: ms, 2: mb, 3+: custom
    document.body.appendChild(this.stats.dom);
  }

  addListeners() {
    this.handlerAnimate = this.animate.bind(this);
    window.addEventListener('resize', this.resize.bind(this));
    window.addEventListener('keyup', this.keyup.bind(this));
    window.addEventListener('mousemove', this.mousemove.bind(this));
    window.addEventListener('click', this.click.bind(this));
    window.addEventListener('mouseup', this.mouseup.bind(this));
    window.addEventListener('touchend', this.mouseup.bind(this));
  }


  // ---------------------------------------------------------------------------------------------
  // PUBLIC
  // ---------------------------------------------------------------------------------------------
  animate() {
    if (this.stats) this.stats.begin();
    this.update();
    if (this.stats) this.stats.end();
    this.raf = requestAnimationFrame(this.handlerAnimate);
  }
  update() {

  }

  // ---------------------------------------------------------------------------------------------
  // EVENT HANDLERS
  // ---------------------------------------------------------------------------------------------

  resize() {}

  keyup(e) {

  }
  mouseup(e) {

  }
  mousemove(e) {

  }

  click(e) {

  }

}
